@import 'assets/scss/utils/mixins';

.container {
	max-width: 900px;
}

.main-container {
	padding-top: 64px;
}

.divider {
	height: 5px;
	background: var(--prim-2);
	opacity: 0.15 !important;
}

.divider-margin {
	margin-top: 48px;
	margin-bottom: 48px;
}

.w-34px {
	width: 34px;
}

.w-52px {
	width: 52px;
}

.w-68px {
	width: 68px;
}

.w-100px {
	width: 100px;
}

.mt-48px {
	margin-top: 48px;
}

.pb-56px {
	padding-bottom: 56px;
}

.connect-btn {
	max-width: 250px;
	height: 40px;
	padding: 0px 16px;
	background: var(--prim-2);
	border: none;
}

.section-margin {
	margin-top: 100px;
}

.section-content {
	max-width: 450px;
	min-width: 300px;
}

.founders-img {
	max-width: 400px;
	max-height: 250px;
}

.empowering-img {
	max-width: 400px;
	max-height: 290px;
}

.tenantin-img {
	max-width: 423px;
	max-height: 265px;
}

.header-img {
	margin-top: 40px;

	.hero-header-img {
		max-width: 900px;
	}
}

.footer-section {
	margin-top: 72px;
	margin-bottom: 32px;
	height: 280px;

	.img-content {
		width: 459px;
	}
}

.section-background-image {
	background-image: url('/assets/images/about/circle-left.svg'), url('/assets/images/about/circle-right.svg');
	background-position:
		top left,
		bottom right;
	background-repeat: no-repeat, no-repeat;
}

@include media-breakpoint-up(md) {
	.hero-header-img {
		max-height: 375px;
	}
}

@include media-breakpoint-down(md) {
	.divider-margin {
		margin-top: 40px;
		margin-bottom: 40px;
	}

	.footer-section {
		margin-top: 40px;
		margin-bottom: 40px;
	}

	.hero-header-img {
		max-height: 375px;
	}
}

@include media-breakpoint-down(lg) {
	.section-margin {
		margin-top: 48px;
	}
}

.numbered-item {
	min-width: 40px;
	min-height: 40px;
	background-color: #ee008814;
}

.list-line {
	border: 1px solid #ee008814;
}
